import {AfterViewInit, Component, ElementRef, Input, ViewChild, ViewEncapsulation} from '@angular/core';
import {Post} from "../timeline.component";
import {OptionsComponent} from "../options/options.component";
import {AsInfiniteScrollComponent, Window, WindowManagerService} from "ng2-simplicity";
import {CommentsComponent} from "../comments/comments.component";
import {LikesComponent} from "../../../shared/likes/likes.component";

@Component({
  selector: 'app-post',
  templateUrl: 'post.component.html',
  styleUrls: ['post.component.css'],
  encapsulation : ViewEncapsulation.None
})
export class PostComponent implements AfterViewInit {

  @Input() model! : Post
  @Input() infinite! : AsInfiniteScrollComponent

  @ViewChild(LikesComponent) likes!: LikesComponent
  @ViewChild("video") video! : ElementRef<HTMLVideoElement>

  constructor(private windowManager : WindowManagerService, private elementRef : ElementRef) {}

  ngAfterViewInit(): void {
    if (this.video) {
      this.video.nativeElement.pause();
    }
  }

  onLike(post: Post) {
    this.likes.model.likes.active = !this.likes.model.likes.active

    let method = "POST";

    secureFetch("service/home/timeline/post", method, post)
      .then(() => {
        if (this.likes.model.likes.active) {
          this.likes.model.likes.count++
        } else {
          this.likes.model.likes.count--
        }
      })
  }

  onOptionsClick(event: MouseEvent, id: string) {
    event.stopPropagation();
    let windowRef = this.windowManager.create(OptionsComponent, {
      header: "Options",
      width: "100px",
      top: event.clientY + "px",
      left: event.clientX - 100 + "px"
    });

    windowRef.instance.infinite = this.infinite
    windowRef.instance.id = id;
  }


}
